import React from 'react'
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import '@fontsource/roboto';
import './App.css';
import MainPage from './pages/MainPage';
import { ProjetosUtil, ProjetosUtilDownloads } from './pages/ProjetosUtil';


function App(props) {
  return (
    <Router basename={process.env.PUBLIC_URL + "/"}>
      <Switch>
        <Route exact path="/">
          <MainPage />
        </Route>
        <Route exact path="/projetosutil/">
          <ProjetosUtil />
        </Route>
        <Route exact path="/projetosutil/downloads">
          <ProjetosUtilDownloads />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
